.person {
  display: flex;
  height: 50px;
  align-items: center;
  border-bottom: 1px solid#676767;
  background-color: #dddddd;
}
.img {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50px;
  height: 50px;
}
img {
  width: 30px;
  height: 30px;
  border-radius: 50%;
}
.name {
  display: flex;
  width: 200px;
}
.btns {
  display: flex;
  margin-left: auto;
  margin-right: 20px;
}
.btns button {
  width: 100px;
  height: 30px;
  border: none;
  border-radius: 3px;
  color: aliceblue;
}
.edit {
  background-color: rgb(83, 177, 240);
}
.del {
  margin-left: 20px;
  background-color: lightcoral;
}
